import Image from "next/image"
import Link from "next/link"

export default function AboutPreview() {
  return (
    <section className="py-20 bg-white">
      <div className="container mx-auto grid md:grid-cols-2 gap-10 items-center">

        {/* Text */}
        <div>
          <h2 className="text-3xl font-bold mb-4">About Us</h2>
          <p className="text-gray-700 mb-6 leading-relaxed">
            We are a digital agency passionate about shaping experiences that help brands grow.
            Our team blends creativity, strategy, and engineering to build products users love.
          </p>

          <Link
            href="/about"
            className="px-4 py-2 bg-gradient-to-r from-indigo-500 to-sky-500 text-white rounded-lg"
          >
            Learn more
          </Link>
        </div>

        {/* Small image preview */}
        <div className="relative h-52 w-full rounded-xl overflow-hidden shadow">
          <Image
            src="/images/about/about1.jpg"
            alt="About"
            fill
            className="object-cover"
          />
        </div>

      </div>
    </section>
  )
}
