import Link from "next/link"
import Image from "next/image"

const projects = [
  { slug: 'project-alpha', title: 'Project Alpha', img: '/images/project/alpha.jpg' },
  { slug: 'project-beta', title: 'Project Beta', img: '/images/project/beta.jpg' },
  { slug: 'project-gamma', title: 'Project Gamma', img: '/images/project/gamma.jpg' },
]

export default function ProjectsPreview() {
  return (
    <section className="py-20 bg-slate-50">
      <div className="container mx-auto">
        <h2 className="text-3xl font-semibold text-center mb-8">Selected Projects</h2>

        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          {projects.map(p => (
            <Link
              key={p.slug}
              href={`/projects/${p.slug}`}
              className="block rounded-lg overflow-hidden shadow bg-white hover:scale-105 transition"
            >
              <div className="relative h-56">
                <Image
                  src={p.img}
                  alt={p.title}
                  fill
                  className="object-cover"
                />
              </div>

              <div className="p-4">
                <p className="font-semibold text-gray-800">{p.title}</p>
              </div>
            </Link>
          ))}
        </div>
      </div>
    </section>
  )
}
